<template>
  <div class="container">
    <h2 class=""><i18n path="common.install"></i18n></h2>

    <hr class="my-2">

    <p><i18n path="common.guide.useNpm"></i18n></p>
    <showcode :trigger="false">
    <pre class="brush:js;">
    npm i v-selectmenu --save
    </pre>
    </showcode>
    <p><i18n path="common.guide.include"></i18n></p>
    <showcode :trigger="false">
    <pre class="brush:js;">
    import Vue from 'vue'
    import vSelectMenu from 'v-selectmenu';
    Vue.use(vSelectMenu, { global config options... });
    </pre>
    </showcode>

    <div class="card p-4 text-monospace">
    <p><i18n path="common.globalConfig"></i18n></p>
    <ul>
        <li>title</li>
        <li>language</li>
        <li>query</li>
        <li>scroll</li>
    </ul>
    </div>

    <h2 class="mt-5"><i18n path="common.deploy"></i18n></h2>
    <hr class="my-2">
    <showcode :trigger="false">
    <script type="syntaxhighlighter" class="brush:html;">
    <![CDATA[
        <template>
          <!-- the default menu type is 'advanced' -->
          <v-selectmenu :data="list" key-field="id" show-field="name">
          </v-selectmenu>
        </template>

        &lt;script&gt;
        export default {
          data(){
            return {
              list: [
                {id:1 ,name:'Chicago Bulls',desc:'芝加哥公牛'},
                {id:2 ,name:'Cleveland Cavaliers',desc:'克里夫兰骑士'},
                {...}
              ]
            }
          }
        }
        &lt;/script&gt;
    ]]>
    </script>
    </showcode>

    <h2 class="mt-5">
      <i18n path="common.example"></i18n>
      <small class="ml-2">
        <a href="https://codepen.io/terry05/pen/WNeEZRd" target="_blank">
          <i18n path="common.codePen"></i18n>
        </a>
      </small>
    </h2>
    <hr class="my-2">

    <h3 class="mt-5"><i18n path="selectmenu.demo.basic"></i18n></h3>

    <div class="card p-5 shadow-sm">
    <h5>Disabled</h5>
    <p>
      <button class="btn btn-outline-secondary" @click="disabled = !disabled">Toggle disabled</button>
    </p>
    <p>
      <v-selectmenu :data="listData" :disabled="disabled">
      </v-selectmenu>
    </p>
    <showcode>
      <script type="syntaxhighlighter" class="brush:html;">
      <![CDATA[
      <v-selectmenu :data="list" :disabled="disabled" >
      </v-selectmenu>

      &lt;script&gt;
      export default {
        data(){
          return {
            list: [ ... ],
            disabled: false
          }
        }
      }
      &lt;/script&gt;
      ]]>
      </script>
    </showcode>

    <h5 class="mt-5"><i18n path="selectmenu.demo.align"></i18n></h5>
    <div class="row mb-3">
      <div class="col-md-4">
        <v-selectmenu :data="menu" type="regular" >
            <button type="button" class="btn btn-secondary">Align left</button>
        </v-selectmenu>
      </div>
      <div class="col-md-4 text-center">
        <v-selectmenu :data="menu" type="regular" align="center" >
            <button type="button" class="btn btn-secondary">Align center</button>
        </v-selectmenu>
      </div>
      <div class="col-md-4 text-right">
        <v-selectmenu :data="menu" type="regular" align="right" >
            <button type="button" class="btn btn-secondary">Align right</button>
        </v-selectmenu>
      </div>
    </div>
    <showcode>
      <script type="syntaxhighlighter" class="brush:html;">
      <![CDATA[
      <v-selectmenu :data="menu" type="regular" align="right" >
        <!-- custom menu caller -->
        <button type="button" class="btn btn-secondary">Align right</button>
      </v-selectmenu>
      ]]>
      </script>
    </showcode>

    <h5 class="mt-5"><i18n path="selectmenu.demo.embed"></i18n></h5>
    <div class="row mb-3">
      <div class="col-md-6">
        <p><i18n path="selectmenu.demo.regular"></i18n></p>
        <v-selectmenu :data="menu" type="regular" :embed="true" >
          <!-- use slot to custom rendering menu row -->
          <template #row="{ row }">
            <span>
              <fa-icon class="fa-lg mr-2" :icon="row.icon.split(',')" v-if="row.icon"></fa-icon>
              <span v-html="row.content"></span>
            </span>
          </template>
        </v-selectmenu>
      </div>
      <div class="col-md-6">
        <p><i18n path="selectmenu.demo.advanced"></i18n></p>
        <v-selectmenu :data="listData" :embed="true" >
        </v-selectmenu>
      </div>
    </div>
    <showcode>
      <script type="syntaxhighlighter" class="brush:html;">
      <![CDATA[
      <v-selectmenu :data="menu" type="regular" :embed="true" >
      </v-selectmenu>
      ]]>
      </script>
    </showcode>

    <h5 class="mt-5"><i18n path="selectmenu.demo.caller"></i18n></h5>
    <p>
      <v-selectmenu :data="menu" type="regular" >
        <template v-slot="{ show, disabled }">
          <button type="button" class="btn btn-primary">
            SelectMenu (show: <b v-text="show"></b>, disabled: <b v-text="disabled"></b>)
          </button>
        </template>
      </v-selectmenu>
    </p>
    <showcode>
      <script type="syntaxhighlighter" class="brush:html;">
      <![CDATA[
      <v-selectmenu :data="menu" type="regular" >
        <!-- custom menu trigger -->
        <button type="button" class="btn btn-primary">SelectMenu</button>
      </v-selectmenu>

      <v-selectmenu :data="menu" type="regular" >
        <!--
        custom menu trigger with scoped slots

        show: the menu dropdown display state
        disabled: v-selectmenu disabled state
        -->
        <template v-slot="{ show, disabled }">
          <button type="button" class="btn btn-primary">
            SelectMenu (show: <b v-text="show"></b>, disabled: <b v-text="disabled"></b>)
          </button>
        </template>
      </v-selectmenu>
      ]]>
      </script>
    </showcode>

    <h5 class="mt-5"><i18n path="selectmenu.demo.customWidth"></i18n></h5>
    <p>
      <v-selectmenu :data="menu" type="regular" :width="500" >
      </v-selectmenu>
    </p>
    <showcode>
      <script type="syntaxhighlighter" class="brush:html;">
      <![CDATA[
      <!-- set menu width to 500px -->
      <v-selectmenu :data="menu" type="regular" :width="500" >
      </v-selectmenu>
      ]]>
      </script>
    </showcode>

    <h5 class="mt-5"><i18n path="selectmenu.demo.rightClick"></i18n></h5>
    <v-selectmenu :data="menu" type="regular" :full-width="true" :right-click="true" >
      <div class="jumbotron text-center" style="margin: 0;">
        <h1><i class="fa fa-fw fa-mouse-pointer"></i> mouse right click to call</h1>
      </div>
    </v-selectmenu>
    <showcode class="mt-3">
      <script type="syntaxhighlighter" class="brush:html;">
      <![CDATA[
      <!--
      rightClick option set to true, enabled menu call method change to mouse right click
      fullWidth option set to true, change container display to black(inline-block by default)
      -->
      <v-selectmenu :data="menu" type="regular" :full-width="true" :right-click="true" >
        <div> the div container </div>
      </v-selectmenu>
      ]]>
      </script>
    </showcode>
    </div>

    <h3 class="mt-5"><i18n path="selectmenu.demo.regular"></i18n></h3>

    <div class="card p-5 shadow-sm">
    <h5>
      <i18n path="selectmenu.demo.format"></i18n>
    </h5>
    <showcode :trigger="false">
      <pre class="brush:js;">
      // the regular menu data list
      [
        // regular menu item with link
        { content: 'Github', url: 'https://github.com' },
        // disabled menu item
        { content: 'Github', url: 'https://github.com', disabled: true },
        // the menu divider
        { content: 'sm-divider' },
        // regular menu item with function
        { content: 'callback', callback: function(){ window.alert('clicked!') },
        // the header menu item
        { content: 'OpenSource', header: true }
      ]
      </pre>
    </showcode>

    <h5 class="mt-5"><i18n path="selectmenu.demo.regular"></i18n></h5>
    <p>
      <v-selectmenu :data="menu" type="regular" ></v-selectmenu>
    </p>
    <showcode>
      <script type="syntaxhighlighter" class="brush:html;">
      <![CDATA[
      <!-- Use default menu caller button -->
      <v-selectmenu :data="menu" type="regular"></v-selectmenu>

      <!-- Use customize menu caller as a button -->
      <v-selectmenu :data="menu" type="regular">
        <button type="button" class="btn btn-secondary">Regular menu</button>
      </v-selectmenu>

      &lt;script&gt;
      export default {
        data(){
          return {
            menu: [
              { content: '163 NetEase', url: 'http://www.163.com' },
              { content: 'Sina', disabled: true, url: 'http://www.sina.com' },
              { content: 'sm-divider' },
              { content: 'GitHub', url: 'https://github.com' },
              { content: 'callback', callback: function(){ window.alert('clicked!'); },
              { ... }
            ]
          }
        }
      }
      &lt;/script&gt;
      ]]>
      </script>
    </showcode>

    <h5 class="mt-5"><i18n path="selectmenu.demo.noTitle"></i18n></h5>
    <p>
      <v-selectmenu :data="menu" type="regular" :title="false" ></v-selectmenu>
    </p>
    <showcode>
      <script type="syntaxhighlighter" class="brush:html;">
      <![CDATA[
      <v-selectmenu :data="menu" type="regular" :title="false" ></v-selectmenu>
