      </script>
    </showcode>

    <h5 class="mt-5"><i18n path="selectmenu.demo.regularHeader"></i18n></h5>
    <p>
      <v-selectmenu :data="headerMenu" type="regular" :title="false" ></v-selectmenu>
    </p>
    <showcode>
      <script type="syntaxhighlighter" class="brush:html;">
      <![CDATA[
      <v-selectmenu :data="menu" type="regular" :title="false" ></v-selectmenu>

      &lt;script&gt;
      export default {
        data(){
          return {
            menu: [
              { content: 'News Site', header: true },
              { content: '163 NetEase', url: 'http://www.163.com' },
              { content: 'Sina', disabled: true, url: 'http://www.sina.com' },
              { content: 'sm-divider' },
              { content: 'Technology Site', header: true },
              { content: 'GitHub', url: 'https://github.com' },
              { ... }
            ]
          }
        }
      }
      &lt;/script&gt;
      ]]>
      </script>
    </showcode>

    <h5 class="mt-5"><i18n path="selectmenu.demo.slot"></i18n></h5>
    <p>
      <v-selectmenu :data="headerMenu" type="regular">
        <!-- use slot to custom rendering menu row -->
        <template #row="{ row }">
          <span>
            <fa-icon class="fa-lg mr-2" :icon="row.icon.split(',')" v-if="row.icon"></fa-icon>
            <span v-html="row.content"></span>
          </span>
        </template>
      </v-selectmenu>
    </p>
    <showcode>
      <script type="syntaxhighlighter" class="brush:html;">
      <![CDATA[
      <v-selectmenu :data="menu" type="regular">
        <!-- use slot to custom rendering menu row(scoped slot with named slot) -->
        <template #row="{ row }">
          <span>
            <!-- display icon by font-awesome -->
            <fa-icon class="fa-lg mr-2" :icon="row.icon.split(',')" v-if="row.icon"></fa-icon>
            <span v-html="row.content"></span>
          </span>
        </template>
      </v-selectmenu>

      &lt;script&gt;
      export default {
        data(){
          return {
            menu: [
              { content: '163 NetEase', url: 'http://www.163.com' },
              { content: 'Sina', disabled: true, url: 'http://www.sina.com' },
              { content: 'sm-divider' },
              { content: 'GitHub', icon: 'fab,github', url: 'https://github.com' },
              { content: 'Reddit', icon: 'fab,reddit', url: 'https://www.reddit.com' },
              {
                content: 'Facebook',
                // custom data field
                icon: 'fab,facebook',
                url: 'https://www.facebook.com',
                disabled: true
              },
              {
                content: 'Twitter',
                icon: 'fab,twitter',
                url: 'https://twitter.com',
                disabled: true
              },
              { ... }
            ]
          }
        }
      }
      &lt;/script&gt;
      ]]>
      </script>
    </showcode>

    <h5 class="mt-5"><i18n path="selectmenu.demo.regularGroup"></i18n></h5>
    <p>
      <v-selectmenu :data="groupMenu" type="regular" ></v-selectmenu>
    </p>
    <showcode>
      <script type="syntaxhighlighter" class="brush:html;">
      <![CDATA[
      <v-selectmenu :data="menu" type="regular" ></v-selectmenu>

      &lt;script&gt;
      export default {
        data(){
          return {
            menu: [
              {
                title: 'Group1',
                list: [ ... ] // the regular menu list
              },
              {
                title: 'Group2',
                list: [ ... ] // the regular menu list
              }
            ]
          }
        }
      }
      &lt;/script&gt;
      ]]>
      </script>
    </showcode>

    <h5 class="mt-5"><i18n path="selectmenu.demo.groupActive"></i18n></h5>
    <p>
      <v-selectmenu :data="groupMenu" type="regular" :active-group="2" ></v-selectmenu>
    </p>
    <showcode>
      <script type="syntaxhighlighter" class="brush:html;">
      <![CDATA[
      <v-selectmenu :data="menu" type="regular" :active-group="2" >
      </v-selectmenu>

      &lt;script&gt;
      export default {
        data(){
          return {
            menu: [
              {
                title: 'Group1',
                list: [ ... ] // the regular menu list
