    <h5 class="mt-5"><i18n path="selectmenu.demo.regularLevel"></i18n></h5>
    <p>
      <v-selectmenu :data="multiLevel" type="regular" ></v-selectmenu>
    </p>
    <showcode>
      <script type="syntaxhighlighter" class="brush:html;">
      <![CDATA[
      <v-selectmenu :data="menu" type="regular" ></v-selectmenu>

      &lt;script&gt;
      export default {
        data(){
          return {
            menu: [
              { content: 'Sports', children: [
                { content:'Fivb', url : 'http://www.fivb.com/'},
                { content:'Fifa', url : 'http://www.fifa.com/'},
                { content:'NBA', children: [
                  { content:'NBA official site', url : 'http://www.nba.com'},
                  { content:'Chicago Bulls', url : 'http://www.nba.com/bulls/'},
                  { content:'Los Angeles Lakers', url : 'www.nba.com/lakers/'}
                ]}
              ]},
              { content: 'sm-divider' },
              { ... }
            ]
          }
        }
      }
      &lt;/script&gt;
      ]]>
      </script>
    </showcode>
    </div>

    <h3 class="mt-5"><i18n path="selectmenu.demo.advanced"></i18n></h3>

    <div class="card p-5 shadow-sm">

    <h5><i18n path="selectmenu.demo.format"></i18n></h5>
    <showcode :trigger="false">
      <pre class="brush:js;">
      // the advanced menu data list
      [
        // any format object as a row
        { id: 1, name: 'Chicago Bulls', desc: '芝加哥公牛', abbr: 'CHI' },
        { id: 2, name: 'Cleveland Cavaliers', desc: '克里夫兰骑士', abbr: 'CLE' },
        { id: 3, name: 'Detroit Pistons', desc: '底特律活塞', abbr: 'DET' },
        { ... }
      ]
      </pre>
    </showcode>

    <h5 class="mt-5"><i18n path="selectmenu.demo.advanced"></i18n></h5>
    <p>
      <v-selectmenu :data="listData" key-field="id" show-field="name">
      </v-selectmenu>
    </p>
    <showcode>
      <script type="syntaxhighlighter" class="brush:html;">
      <![CDATA[
      <!--
      set keyField option to specify a data field as key field
      set showField option to specify a data field as display field
      -->
      <v-selectmenu :data="menu" key-field="id" show-field="name" >
      </v-selectmenu>
      ]]>
      </script>
    </showcode>

    <h5 class="mt-5"><i18n path="selectmenu.demo.advancedShowField"></i18n></h5>
    <p>
      <v-selectmenu :data="listData" :show-field="rowFormat">
      </v-selectmenu>
    </p>
    <showcode>
      <script type="syntaxhighlighter" class="brush:html;">
      <![CDATA[
      <v-selectmenu :data="menu" :show-field="rowFormat" >
      </v-selectmenu>

      &lt;script&gt;
      export default {
        methods: {
          rowFormat (row) {
            if (row) {
              return row.name + ' (' + row.abbr + ')'
            }
          }
        }
      }
      &lt;/script&gt;
      ]]>
      </script>
    </showcode>

    <h5 class="mt-5"><i18n path="selectmenu.demo.slot"></i18n></h5>
    <p>
      <v-selectmenu :data="listData">
        <!-- use slot to custom rendering menu row -->
        <template #row="{ row }">
          <span v-html="row.name"></span>
          <span class="mx-2">-</span>
          <span v-html="row.abbr"></span>
        </template>
      </v-selectmenu>
    </p>
    <showcode>
      <script type="syntaxhighlighter" class="brush:html;">
      <![CDATA[
      <v-selectmenu :data="menu">
        <!-- use slot to custom rendering menu row(scoped slot with named slot) -->
        <template #row="{ row }">
          <span v-html="row.name"></span>
          <span class="mx-2">-</span>
          <span v-html="row.abbr"></span>
        </template>
      </v-selectmenu>
      ]]>
      </script>
    </showcode>

    <h5 class="mt-5"><i18n path="selectmenu.demo.noTitle"></i18n></h5>
