      <v-selectmenu :data="menu" :scroll="false">
      </v-selectmenu>
      ]]>
      </script>
    </showcode>

    <h5 class="mt-5"><i18n path="selectmenu.demo.advancedInit"></i18n></h5>
    <pre class="bg-light p-3">{{JSON.stringify(value1, null, 4)}}</pre>
    <p>
      <v-selectmenu :data="listData" v-model="value1">
      </v-selectmenu>
    </p>
    <showcode>
      <script type="syntaxhighlighter" class="brush:html;">
      <![CDATA[
      <v-selectmenu :data="menu" key-field="id" v-model="value1" >
