    </div>

    <h2 class="mt-5"><i18n path="common.options"></i18n></h2>
    <table class="table table-striped option-table">
      <thead>
      <tr>
        <th><i18n path="common.tbTitle.name"></i18n></th>
        <th><i18n path="common.tbTitle.type"></i18n></th>
        <th><i18n path="common.tbTitle.desc"></i18n></th>
        <th><i18n path="common.tbTitle.required"></i18n></th>
        <th><i18n path="common.tbTitle.default"></i18n></th>
      </tr>
      </thead>
      <tbody>
      <tr>
        <td>value/v-model</td>
        <td>string</td>
        <td><i18n path="selectmenu.option.value"></i18n></td>
        <td>false</td>
        <td></td>
      </tr>
      <tr>
        <td>data</td>
        <td>array</td>
        <td><i18n path="selectmenu.option.data"></i18n></td>
        <td>true</td>
        <td></td>
      </tr>
      <tr>
        <td>key-field</td>
        <td>string</td>
        <td><i18n path="selectmenu.option.keyField"></i18n></td>
        <td>false</td>
        <td>id</td>
      </tr>
      <tr>
        <td>show-field</td>
        <td>string|function</td>
        <td><i18n path="selectmenu.option.showField"></i18n></td>
        <td>false</td>
        <td>name</td>
      </tr>
      <tr>
        <td>type</td>
        <td>string</td>
        <td>
          <p><i18n path="selectmenu.option.type"></i18n></p>
          <ul>
            <li><code class="mr-3">'regular'</code> <i18n path="selectmenu.demo.regular"></i18n></li>
            <li><code class="mr-3">'advanced'</code> <i18n path="selectmenu.demo.advanced"></i18n></li>
          </ul>
        </td>
        <td>false</td>
        <td>'advanced'</td>
      </tr>
      <tr>
        <td>align</td>
        <td>string</td>
        <td>
          <p><i18n path="selectmenu.option.align"></i18n></p>
          <ul>
            <li>"left"</li>
